import React, { Component } from "react";
import "./App.css";
import { Button, ModalBody } from "reactstrap";
import inputList from "./input.json";

const timestamp = Date.now();
var dateTimeText = "";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      PhoneNumber: "",
      TrackingNum: "",
      inputList: []
    };
  }

  handleSubmit = event => {
    var output = "Not Found";

    this.setState({ TrackingNum: output });
    dateTimeText = new Intl.DateTimeFormat("en-US", {
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit"
    }).format(timestamp);
  };

  handleInputChange = event => {
    this.setState({ PhoneNumber: event.target.value });
  };

  render() {
    if (inputList.length > 0) {
      var trackingNumber = inputList.map(obj => {
        if (
          obj.num.replace("-", "").replace("-", "") ===
          this.state.PhoneNumber.replace("-", "").replace("-", "")
        )
          return <div>{obj.tnum}</div>;
      });
    }

    const hStyle = { color: "red" };

    return (
      <div>
        <ModalBody>
          <h3> ROPHEKA Tracking System </h3>
          <div className="form-group">
            {" "}
            <label className="form-label">
              ใส่หมายเลขโทรศัพท์: {"   "}
              <input
                className="form-input"
                type="text"
                value={this.state.PhoneNumber}
                placeholder={"Please input the number"}
                onChange={this.handleInputChange}
              />
            </label>
          </div>
          <div className="form-group">
            {" "}
            <label className="form-label">
              หมายเลข Tracking คือ
              <h3 style={hStyle}>{trackingNumber}</h3>
            </label>
          </div>{" "}
          {dateTimeText}
        </ModalBody>
      </div>
    );
  }
}

export default App;
